const uncompletedIcon = <svg t="1594690318567" className="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="3332" width="200" height="200"><path d="M512 0C229.232 0 0 229.232 0 512s229.232 512 512 512 512-229.232 512-512S794.768 0 512 0z m0 928C282.256 928 96 741.744 96 512 96 282.24 282.256 96 512 96s416 186.24 416 416c0 229.744-186.256 416-416 416z" fill="#ffffff" p-id="3333"></path></svg>

const INIT_TODOS = [
  { text: 'Learn react', isCompleted: false },
  { text: 'Learn hooks', isCompleted: false },
  { text: 'Having lunch', isCompleted: false },
];

function TodoForm( {addTodo} ) {
  const [inputStateValue, setInputStateValue] = useState('');
  const handleSubmit = (e) => {
    e.preventDefault();
    if (!inputStateValue) return;
    addTodo(inputStateValue); 
    setInputStateValue('');
  }


  return (
    <form onSubmit={handleSubmit}>
      <input 
      className="input" 
      value={inputStateValue} 
      onChange={(e) => setInputStateValue(e.target.value)}
      />
    </form>
  )
}

function Todo( {todo, index, removeTodo, completeTodo} ) {
  return (
    <div className="todo" onClick={() => completeTodo(index)}>
      <div 
        className="task"
        style={{
          textDecoration: todo.isCompleted ? 'line-through' : ''
        }}
      >
        {todo.isCompleted ? completedIcon : uncompletedIcon}
        {todo.text}
      </div>
      <a 
        className="remove" 
        href="#!" 
        onClick={(e) => removeTodo(e, index)}
      >
        {removeIcon}
      </a>
    </div>
  )
}

function App() {
  const[todos, setTodos] = useState( 
    JSON.parse(localStorage.getItem('todos')) || INIT_TODOS
  );
  useEffect(() => {
    localStorage.setItem('todos', JSON.stringify(todos));
  }, [todos]);

  const addTodo = newTodo => {
    const newTodos = [{text: newTodo, isCompleted: false}, ...todos];
    setTodos(newTodos);
  };

  const removeTodo = (e, index) => {
    e.preventDefault();
    e.stopPropagation();
    const newTodos = [...todos];
    newTodos.splice(index, 1);
    setTodos(newTodos);
  };

  const completeTodo = (index) => {
    const newTodos = [...todos];
    newTodos[index].isCompleted=true;
    setTodos(newTodos); 
  }

  return (
    <div className="app">
        <div className="todo-list">
          <TodoForm addTodo={ addTodo }/>
          {
            todos.map((todo, index) => (
              <Todo 
                key={index} 
                todo={todo} 
                index={index}
                removeTodo={removeTodo} 
                completeTodo={completeTodo}
              />
            ))
          }
        </div>
    </div>
  );
}

export default App;
